<template>
    <div id="bank" style="padding:5% 20%;width:50%;text-align:center">
      <el-form :model="form" label-width="100px" style="padding:4% 20% 5% 15%;background-color:#fff;border-radius:8px;">
        <div style="font-size:17px;margin:7% 0px;">
            <b>创建银行</b>
        </div>
        <el-form-item label="银行名称：">
            <el-input v-model="form.bankId" placeholder="请输入银行名称"></el-input>
        </el-form-item>
         <!-- <el-form-item> -->
            <el-row>
              <el-col :span="8" style="text-align:right">
                <el-button type="primary" @click="createBank">创建银行</el-button>
              </el-col>
              <el-col :span="8" style="text-align:right">
                <el-button type="success" @click="storage">保存记录</el-button>
              </el-col>
              <el-col :span="8" style="text-align:right">
                <el-button type="primary" @click="getSecrete">下载密钥</el-button>
              </el-col>
            </el-row>
            <!-- <el-button type="primary" @click="createBank">创建银行</el-button>
            <el-button type="success" @click="storage">保存记录</el-button>
            <el-button type="primary" @click="getSecrete">下载密钥</el-button>
        </el-form-item> -->

      </el-form>
    </div>
</template>

<script>
export default {
  name: 'Bank',
  data () {
    return {
      form: {}
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var localStr = localStorage.getItem('bank')
      if (localStr === null) {
        return
      }
      this.form = JSON.parse(localStorage.getItem('bank'))
    },
    storage () {
      localStorage.setItem('bank', JSON.stringify(this.form))
    },
    createBank () {
      var that = this
      this.$axios.post('/v1/bank', JSON.stringify(this.form))
        .then(function (response) {
          if (response.data.validCode === 0) {
            that.$message.success('创建成功')
          } else {
            that.$message.error(response.data.msg)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    getSecrete () {
      var elemIF = document.createElement('iframe')
      elemIF.src = '/v1/bank/' + this.form.bankId + '/key'
      elemIF.style.display = 'none'
      document.body.appendChild(elemIF)
    }
  }
}
</script>



